/**
 * 密码
 * 最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isPassword(str) {
  const reg = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
  return reg.test(str);
}

/**
 * 是否是用户名
 * 4到16位（字母，数字，下划线，减号）
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isUsername(str) {
  const reg = /^[a-zA-Z0-9_-]{4,16}$/;
  return reg.test(str);
}

/**
 * 是否是邮箱
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isEmail(str) {
  const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
  return reg.test(str);
}

/**
 * 是否是手机号码（中国）
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isChinaPhone(str) {
  const reg = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
  return reg.test(str);
}

/**
 * 是否是IP
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isIP(str) {
  const reg = /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
  return reg.test(str);
}

/**
 * 是否是端口号
 * @param {String} str 字符串
 * @returns {Boolean}
 */
function isPort(port) {
  if (/^[1-9]\d*|0$/.test(port) && port * 1 >= 0 && port * 1 <= 65535) {
    return true
  }
  return false;
}

export default {
  isPassword,
  isUsername,
  isEmail,
  isChinaPhone,
  isIP,
  isPort
};
